.product-card {
  padding-top: 25px;
  padding-bottom: 60px;
  display: flex;
  gap: 40px;

  &__img-wrapper {
    max-width: 664px;
    min-width: 220px;
    padding: 15px;
  }

  &__img {
    width: 100%;
    height: auto;
  }

  &__data {
    max-width: 628px;
  }

  &__top-note {
    font-weight: 500;
    font-size: 14px;
    line-height: 130%;
    color: #1FD85D;
  }

  &__title {
    font-weight: 400;
    font-size: 30px;
    line-height: 150%;
    color: $_1;
    margin-bottom: 10px;

    &_bold {
      font-weight: 800;
    }
  }

  &__product-type-box {
    display: flex;
    margin-bottom: 15px;
  }

  &__product-type-icon {
    margin-right: 5px;
  }

  &__product-type-text {
    font-weight: 400;
    font-size: 12px;
    line-height: 130%;
    color: $_2;
  }

  &__product-price-box {
    max-width: 489px;
    min-height: 59px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 20px;
    gap: 10px;
  }

  &__product-price {
    font-weight: 800;
    font-size: 30px;
    line-height: 130%;
    color: $_1;
  }

  &__amount-box {
    height: 23px;
    width: 113px;
    border-radius: 1000px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__dec {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 34px;
    height: 23px;
    background: linear-gradient(90deg, rgba(255, 198, 80, 0.3) 0%, rgba(254, 202, 110, 0.3) 97.25%);
    border-radius: 1000px;
    font-weight: 300;
    font-size: 12px;
    line-height: 15px;
    text-align: center;
    border: none;
    color: $_2;
    cursor: pointer;
  }

  &__count {
    font-weight: 500;
    font-size: 14px;
    line-height: 130%;
    color: $_2;
  }

  &__inc {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 34px;
    height: 23px;
    background: linear-gradient(90deg, rgba(255, 198, 80, 0.3) 0%, rgba(254, 202, 110, 0.3) 97.25%);
    border-radius: 1000px;
    font-weight: 300;
    font-size: 12px;
    line-height: 15px;
    text-align: center;
    border: none;
    color: $_2;
    cursor: pointer;
  }

  &__product-cartBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;

    background-color: $_5;
    border-radius: 80px;
    border: none;

    width: 184px;
    height: 59px;

    font-weight: 700;
    font-size: 10px;
    line-height: 12px;
    letter-spacing: 0.15em;
    color: #FFFFFF;
    cursor: pointer;

    &_mobile {
      display: none;
    }
  }

  &__product-cartBtn-icon {
    height: 23px;
    width: 23px;
  }

  &__ad-box {
    max-width: 599px;
    min-height: 77px;
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 35px;
  }

  &__share-link {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 77px;
    min-height: 77px;

    background: #FFFFFF;
    box-shadow: 0px 15px 70px -11px rgba(43, 28, 1, 0.04);
    border-radius: 10px;
    border: none;

    &_mobile {
      display: none;
    }
  }

  &__share-icon {
    height: 24px;
    width: 24px;
  }

  &__ad-text-box {
    height: 77px;
    width: 316px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #FFFFFF;
    box-shadow: 0px 15px 70px -11px rgba(43, 28, 1, 0.04);
    border-radius: 10px;
  }

  &__ad-text {
    width: 245px;
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    text-align: center;
    color: $_2;

    &_bold {
      font-weight: 700;
    }
  }

  &__priceListBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    width: 186px;
    height: 77px;
    background: #FFFFFF;
    box-shadow: 0px 15px 70px -11px rgba(43, 28, 1, 0.04);
    border-radius: 10px;

    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    color: $_2;
    border: none;
  }

  &__priceListBtn-icon {
    width: 17px;
    height: 17px;
  }

  &__prop-list {
    list-style: none;
    margin-bottom: 30px;
  }

  &__prop {
    font-weight: 300;
    font-size: 14px;
    line-height: 150%;
    color: $_2;

    &_value {
      font-weight: 500;
      font-size: 14px;
      line-height: 150%;
      color: $_1;
    }
  }

  &__description-btn {
    display: flex;
    align-items: center;
    width: 90px;
    height: 24px;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: $_1;
    background: none;
    border: none;
    cursor: pointer;
  }

  &__description-btn-text {
    margin-right: 5px;
  }

  &__description-btn-icon {
    height: 7px;
    width: 7px;
  }

  &__description {
    margin-top: 15px;
    width: 611px;
    height: 72px;
    font-weight: 300;
    font-size: 12px;
    line-height: 150%;
    color: $_2;
  }

  &__divider {
    margin: 20px 0;
    width: 269.5px;
    opacity: 0.3;
    border-top: 1px dashed $_2;
  }

  &__properties-btn {
    display: flex;
    align-items: center;
    width: 90px;
    height: 24px;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: $_1;
    background: none;
    border: none;
    cursor: pointer;
  }

  &__properties-btn-text {
    margin-right: 5px;
  }

  &__properties-btn-icon {
    height: 7px;
    width: 7px;
  }

  &__all-properties-list {
    list-style: none;
    margin-top: 15px;
  }
}